
import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { Switch, Route, BrowserRouter as Router } from 'react-router-dom';
import { fetchOrganization } from './redux';
import CFG from './config.json';
import Home from './comp/home';
import About from './comp/about';
import AboutDetails from './comp/about-details';
import Services from './comp/services';
import ServiceDetails from './comp/service-details';
import Portfolio from './comp/portfolio';
import PortfolioDetails from './comp/portfolio-details';
import Faq from './comp/faq';
import Contacts from './comp/contacts';
import PrivacyPolicy from './comp/privacy';
import TermsOfService from './comp/terms';
import Confidentiality from './comp/confidentiality';
import BlogHome from './comp/blog/blog-home';
import BlogDetails from './comp/blog/blog-details';
import Error400 from './comp/error/400';
import Error404 from './comp/error/404';
import Error500 from './comp/error/500';

function App({ fetchOrganization, organizationData }) {
  const [ orgParameter, setOrgParameter ] = useState(CFG.serialCode);

  useEffect(() => fetchOrganization(orgParameter), []);

  return (
    <Router>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/about" component={About} />
        <Route exact path="/about-details" component={AboutDetails} />
        <Route exact path="/services" component={Services} />
        <Route exact path="/service-details:index" component={ServiceDetails} />
        <Route exact path="/portfolio" component={Portfolio} />
        <Route exact path="/portfolio-details:index" component={PortfolioDetails} />
        <Route exact path="/faq" component={Faq} />
        <Route exact path="/contacts" component={Contacts} />
        <Route exact path="/blog-home" component={BlogHome} />
        <Route exact path="/blog-details:uuid" component={BlogDetails} />
        <Route exact path="/terms-of-service" component={TermsOfService} />
        <Route exact path="/privacy-policy" component={PrivacyPolicy} />
        <Route exact path="/confidentiality" component={Confidentiality} />
        <Route exact path="/400" component={Error400} />
        <Route exact path="/404" component={Error404} />
        <Route exact path="/500" component={Error500} />
        <Route component={Error404} />
      </Switch>
    </Router>
  );
}

const mapStateToProps = state => {
  return {
    organizationData: state.organizationData,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    fetchOrganization: parameter => dispatch(fetchOrganization(parameter)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(App);